a {
  text-decoration: none;
  color: white;
}

a:hover {
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 15s linear;
  }
}

.mob_alert {
  width: 100%;
  height: 100vh;
  background-color: rgba(58, 58, 58, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50000000;
}

.mob_alert h3 {
  width: 80%;
  padding: 40px 20px;
  box-sizing: border-box;
  font-size: 1.1rem;
  margin: 0 auto;
  background-color: rgb(124, 53, 172);
  border-radius: 30px;
  position: relative;
  top: 36%;
  color: white;
  border: 1px solid rgb(224, 222, 222);
}

#jinwrap {
  width: 100%;
  padding-bottom: 150px;
  overflow: hidden;
}

.bacground {
  width: 100%;
  height: 500px;

  background-size: cover;
  position: relative;
}

.bg1 {
  background-image: url(./aesset/mlb_b.png);
}
.bg2 {
  background-image: url(./aesset/mlb_b2.png);
}
.bg3 {
  background-image: url(./aesset/mlb_b3.png);
}
.bg4 {
  background-image: url(./aesset/mlb_b4.png);
}
.navjin {
  background-color: rgb(67, 26, 114) !important;
}

.bacground h3 {
  width: 100%;
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.8rem;
  color: rgb(92, 27, 136);
  font-weight: bold;
}

.new {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 40px;
  font-weight: 400;
  position: relative;
}

.new .sort_b {
  display: inline-block;
  width: 50px;
  font-size: 0.7rem;
  line-height: 27px;
  position: absolute;
  top: 0;
  right: 15px;
  background: rgb(140, 63, 192);
  color: white;
  font-weight: 500;
  border: none;
  border-radius: 10px;
}

.new .sort_b:hover {
  background: white;
  color: rgb(140, 63, 192);
  line-height: 25px;
  font-weight: 600;
  border: 1px solid rgb(140, 63, 192);
}

.cont2 {
  width: 100%;
  padding: 0 10px;
  padding-bottom: 30px !important;
  box-sizing: border-box;
}

.cont2 .jinrow {
  margin: 0 auto;
  width: 95% !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.jinrow .mlb_col {
  width: 32%;
  text-align: center;
}

.jinrow .mlb_col {
  text-align: center;
  margin-bottom: 32px;
}

.mlb_col .square {
  width: 100%;
  margin-top: 16px;
  padding: 0 2px;
  box-sizing: border-box;
}

.jinrow .mlb_col h4 {
  text-align: center;
  color: #282c34;
  font-size: 0.92rem;
  font-weight: bold;
}

.jinrow .mlb_col p {
  color: #282c34;
  font-size: 0.79rem;
  font-weight: 500;
  margin: 0;
}
#jinhee {
  width: 100%;
  display: flex;
}

#cont3 {
  width: 50%;
  background: rgb(145, 82, 187);
  height: 400px;
  box-shadow: 10px 10px 7px rgba(61, 22, 22, 0.35);

  transition: cubic-bezier(1, 0.27, 0.25, 1) 0.3s;
}

#cont3:nth-child(1) {
  border-radius: 30px 0 0 30px;
  transform: translateX(-110%);
}
#cont3:nth-child(2) {
  border-radius: 0 30px 30px 0;
  transform: translateX(110%);
}
.on {
  transform: translateX(0) !important;
}
/* ------------------------------- */

@media all and (max-width: 750px) {
  .cont2 {
    width: 100%;
    margin-top: 35px;
    padding: 0 10px;
    box-sizing: border-box;
  }
  .cont2 .jinrow {
    margin: 0 auto;
    width: 100% !important;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .jinrow .mlb_col {
    width: 49.2%;
    text-align: center;
  }
}

.fail {
  width: 83%;
  height: 60px;
  background: rgba(155, 152, 152, 0.2);
  line-height: 60px;
  margin: 35px auto;
  text-align: center;
  color: rgb(92, 35, 156);
  font-size: 1.05rem;
  border-radius: 15px;
  box-shadow: 5px 6px 4px rgba(66, 66, 66, 0.2);
}

.spinner_j {
  position: relative;
  top: 5px;
  margin-bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.more {
  width: 83%;
  margin: 30px auto;
  background: white;
  line-height: 35px;
  border-radius: 10px;
  border: 1.75px solid rgb(82, 32, 138);
  color: rgb(67, 26, 114);
}

.more:hover {
  background: rgb(98, 48, 155);
  color: white;
  border-color: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.prod img {
  margin-top: 20px;
}

.mt-4 {
  text-align: left;
  padding: 0 30px;
  padding-bottom: 25px;
  box-sizing: border-box;
}

.pt-5 {
  padding: 0 !important;
  line-height: 30px;
}

.aleat_t {
  width: 85%;
  background: rgb(170, 86, 170);
  padding: 14px;
  box-sizing: border-box;
  margin: 45px auto;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  border-radius: 50px;
}
.popup {
  width: 100%;
  padding: 40px 0;
  text-align: left !important;
  padding-bottom: 20px;
  background: rgb(238, 220, 250);
  position: fixed;
  left: 0;
  bottom: 0;
  border-radius: 50px 50px 0 0;
  border: 1.8px solid rgb(92, 27, 136);
}

.popup select {
  background: white;
  width: 87%;
  height: 40px;
  line-height: 60px;
  color: rgb(45, 11, 68);
  border: 1.7px solid rgb(92, 27, 136);
  border-radius: 10px;
  display: block;
  margin: 0 auto;
  margin-bottom: 7px;
}

.popup .total {
  width: 87%;
  display: flex;
  margin: 0 auto;
  margin-top: 18px;
}

.popup .total h3 {
  width: 50%;
  font-size: 1.4rem;
}

.popup .total h3:nth-child(2) {
  text-align: right;
  display: flex;
}
.popup .total h3:nth-child(2) span {
  display: block;
  line-height: 24px;
}
.popup .total h3:nth-child(2) .s1 {
  width: 25%;
  text-align: left;
  font-size: 1.02rem;
  font-weight: bold;
  color: rgb(92, 27, 136);
}
.popup .total h3:nth-child(2) .s2 {
  width: 75%;
  text-align: right;
}
input {
  width: 63%;
  margin-top: 20px;
  line-height: 35px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.8);
  padding-left: 10px;
  box-sizing: border-box;
  animation: jinhee 1.3s infinite;
  box-shadow: 0 0 0 0 rgba(249, 178, 8, 0.5);
}

input::placeholder {
  font-size: 16px;
  color: darkblue;
}

input:hover {
  animation: none;
}

@keyframes jinhee {
  0% {
    -moz-transform: scale(0.95);
    -ms-transform: scale(0.95);
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
  }
  70% {
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -webkit-transform: scale(1);
    transform: scale(1);
    box-shadow: 0 0 0 30px rgba(249, 178, 8, 0);
  }
  100% {
    -moz-transform: scale(0.95);
    -ms-transform: scale(0.95);
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(249, 178, 8, 0);
  }
}

.slick-dots {
  width: 85% !important;
  bottom: 12px !important;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000000 !important;
  height: 28px;
}

.page_ani-enter {
  transform: translate3d(100%, 0, 0);
}

.page_ani-enter-active {
  transform: translate3d(0, 0, 0);
  transition: all 500ms;
}
